@use 'base/variables' as v;
@use 'base/mixins' as m;

.listados-servicios{
    display: grid;
    gap: 2rem;
    @include m.desktop{
        @include m.grid(2,2rem);
    }
    .servicio{
        background-color: v.$blanco;
        padding: 2rem;
        border-radius: 1rem;
        text-align: center;
        transition: transform 300ms ease-in;
        &:hover{
            transform: scale(1.1);
        }
        p{
            color:v.$negro;
            margin: 0 0  1rem 0;
        }
        .precio-servicio{
            color: v.$azul;
            font-weight: 900;
            font-family: v.$fuente_heading;
            font-size: 3rem;
            margin: 0;
        }
        &.seleccionado{
            background-color: v.$azul;
            p{
                color:v.$blanco;
            }
        }
    }
    
}