@use 'base/variables' as v;
@use 'base/mixins' as m;
.citas{
    list-style: none;
    padding: 0;

    li{
        padding: 1rem;
        border-top: 1px solid darken(v.$azul,10);

    }
    p{
        font-weight: bold;
        color: v.$azul;
    }
    span,.servicio{
        font-weight: normal;
        color: v.$blanco;
    }
    .servicios{
        text-align: center;
        background-color: darken(v.$verde, 10);
    }
    .servicio{
        background-color: darken(v.$verde, 38);
        margin: 0;
        padding: 1.5rem 1rem;
        text-align: center;
        span{
            font-weight: bold;
            color: v.$verde
        }
    }
    .total{
        font-family: v.$fuente_heading;
        text-align: right;
        font-size: 3rem;
        span{
            font-weight: bolder;
        }
    }
    
}
.servicios{
    @extend .citas;
    .acciones{
        margin: 0;
    }
    .btn, .btn-eliminar{
        margin: 1rem 0;
    }
}


