/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #0e0b16;
  color: #FFFFFF;
  font-family: "Montserrat Alternates", sans-serif;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  body {
    height: 100%;
  }
}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Trirong", serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.nombre__pag {
  text-align: center;
  margin: 5rem 0;
  font-weight: 700;
}

.descripcion__pag {
  text-align: center;
}

@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}
.contenedor-app .imagen {
  background-image: url(../img/kiri_spa.jpg);
  background-size: cover;
  background-position: center center;
  height: 20rem;
}
@media (min-width: 768px) {
  .contenedor-app .imagen {
    height: auto;
  }
}
.contenedor-app .app {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .contenedor-app .app {
    overflow-y: scroll;
  }
}
.contenedor-app .app .padding-content {
  width: 95%;
  margin: 0 auto;
}
.contenedor-app .barra {
  padding: 1rem;
  margin: 5rem 0;
  text-align: center;
  background-color: #191427;
  border-radius: 2rem;
  font-family: "Trirong", serif;
  border: 1px #431654 solid;
}
@media (min-width: 768px) {
  .contenedor-app .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.contenedor-app .barra .btn {
  background-color: #a239ca;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  margin: 0;
}
@media (min-width: 768px) {
  .contenedor-app .barra .btn {
    width: auto;
  }
}
.contenedor-app .barra .btn:hover {
  background-color: #832ca4;
  cursor: pointer;
}
.contenedor-app .barra p {
  margin: 1rem 2rem;
}
.contenedor-app .barra-servicios {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .contenedor-app .barra-servicios {
    display: flex;
    justify-content: space-evenly;
  }
}
.contenedor-app .barra-servicios .btn {
  margin: 2rem 0;
}

.seccion {
  padding: 5rem 0;
  display: none;
}
.seccion .text-center {
  text-align: center;
}

.mostrar {
  display: block;
}

.ocultar {
  display: block;
}

.listados-servicios {
  display: grid;
  gap: 2rem;
}
@media (min-width: 1024px) {
  .listados-servicios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.listados-servicios .servicio {
  background-color: #FFFFFF;
  padding: 2rem;
  border-radius: 1rem;
  text-align: center;
  transition: transform 300ms ease-in;
}
.listados-servicios .servicio:hover {
  transform: scale(1.1);
}
.listados-servicios .servicio p {
  color: #0e0b16;
  margin: 0 0 1rem 0;
}
.listados-servicios .servicio .precio-servicio {
  color: #4717f6;
  font-weight: 900;
  font-family: "Trirong", serif;
  font-size: 3rem;
  margin: 0;
}
.listados-servicios .servicio.seleccionado {
  background-color: #4717f6;
}
.listados-servicios .servicio.seleccionado p {
  color: #FFFFFF;
}

.contenido-resumen p {
  margin-bottom: 1rem;
}
.contenido-resumen span {
  color: #a239ca;
  font-weight: 700;
  font-size: 2.4rem;
}

.contenedor-serv {
  text-align: center;
  border-bottom: 1px solid #4717f6;
  padding: 1rem 0;
  background-color: #241c38;
}
.contenedor-serv:last-of-type {
  margin-bottom: 5rem;
}

.citas, .servicios {
  list-style: none;
  padding: 0;
}
.citas li, .servicios li {
  padding: 1rem;
  border-top: 1px solid #3408d2;
}
.citas p, .servicios p {
  font-weight: bold;
  color: #4717f6;
}
.citas span, .servicios span, .citas .servicio, .servicios .servicio {
  font-weight: normal;
  color: #FFFFFF;
}
.citas .servicios, .servicios .servicios {
  text-align: center;
  background-color: #832ca4;
}
.citas .servicio, .servicios .servicio {
  background-color: #290e33;
  margin: 0;
  padding: 1.5rem 1rem;
  text-align: center;
}
.citas .servicio span, .servicios .servicio span {
  font-weight: bold;
  color: #a239ca;
}
.citas .total, .servicios .total {
  font-family: "Trirong", serif;
  text-align: right;
  font-size: 3rem;
}
.citas .total span, .servicios .total span {
  font-weight: bolder;
}

.servicios .acciones {
  margin: 0;
}
.servicios .btn, .servicios .btn-eliminar {
  margin: 1rem 0;
}

.form {
  margin-top: 5rem;
  width: 100%;
}

.campo {
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
}
.campo label {
  flex: 0 0 10rem;
}
.campo input {
  width: 100%;
  border: none;
  padding: 1.25rem;
  border-radius: 1rem;
}
.campo input:disabled {
  background-color: #392d5a;
  color: #FFFFFF;
  cursor: not-allowed;
}

.btn {
  background-color: #4717f6;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .btn {
    width: auto;
  }
}
.btn:hover {
  background-color: #3408d2;
  cursor: pointer;
}

.ocultar {
  display: none;
}

.mostrar {
  display: block;
}

.btn-eliminar {
  background-color: #cb0000;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .btn-eliminar {
    width: auto;
  }
}
.btn-eliminar:hover {
  background-color: #980000;
  cursor: pointer;
}

.bg-azul {
  background-color: #150357;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.acciones a {
  color: #FFFFFF;
  display: block;
  margin-bottom: 2.5rem;
  text-align: center;
}

.alerta {
  padding: 1.25rem;
  color: #FFFFFF;
  width: 100%;
  display: block;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
}
.alerta.error {
  background-color: #cb0000;
}
.alerta.exito {
  background-color: #a239ca;
}

.contenido_img {
  background-image: url(../img/mensaje.png);
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
  height: 70vh;
  margin-bottom: -4rem;
}

@media (min-width: 768px) {
  .tabs {
    display: flex;
  }
}
.tabs button {
  background-color: #4717f6;
  border: none;
  color: #FFFFFF;
  padding: 1rem;
  display: block;
  width: 100%;
  margin-bottom: 1rem;
  text-transform: uppercase;
  font-weight: 700;
  font-family: "Trirong", serif;
  font-size: 2rem;
  transition: background-color 300ms ease-in;
}
.tabs button:hover {
  cursor: pointer;
  background-color: #3408d2;
}
.tabs button.actual {
  background-color: #FFFFFF;
  color: #4717f6;
}

@media (min-width: 480px) {
  .paginacion {
    display: flex;
    justify-content: space-around;
  }
}
.paginacion button {
  margin: 1rem 0;
}