@use 'variables' as v;

h1, h2, h3 {
    font-family: v.$fuente_heading;
    margin: 0 0 calc(v.$separacion / 2) 0 ;
    font-weight: 900;
    color: v.$blanco;
    text-align: center;
}

h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3rem;
    text-align: center;
}
p {
    color: v.$blanco;
    font-size: 2rem;
}
.nombre__pag{
    text-align: center;
        margin: 5rem 0;
        font-weight: v.$bold;
}
.descripcion__pag{
    
    text-align: center;
}
