@use 'base/variables' as v;
@use 'base/mixins' as m;
.contenedor-app{
    @include m.tablet{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        min-height: 100vh;
    }
    .imagen{
        background-image: url(../img/kiri_spa.jpg);
        background-size: cover;
        background-position: center center;
        height: 20rem;
        @include m.tablet{
            height: auto;
        }
    }
    .app{
      
        padding: 3rem 0;
        @include m.tablet{
            overflow-y: scroll;
        }
        .padding-content{
            width: 95%;
            margin: 0 auto;
        }
    }
    .barra{
        padding: 1rem;
        margin: v.$separacion 0;
        text-align: center;
        background-color: lighten(v.$negro,5%);
        border-radius: 2rem;
        font-family: v.$fuente_heading;
        border: 1px darken(v.$verde,30%) solid;
        @include m.tablet{
            display:flex;
            justify-content: space-between;
            align-items: center
        }
        .btn{
            @include m.boton(v.$verde, v.$blanco);
            margin: 0;
        }
        p{
            margin: 1rem  2rem;
        }
       
    }
    .barra-servicios{
        margin-bottom: v.$separacion;
        @include m.tablet{
            display: flex;
            justify-content: space-evenly;
        }
        .btn{
            margin: 2rem 0;
        }
    }
    
}