@use 'base/variables' as v;
@use 'base/mixins' as m;
.form{
    margin-top: v.$separacion;
    width: 100%;
}
.campo{
    display: flex;
    margin-bottom: 2rem;
    align-items: center;
    label{
        flex: 0 0 10rem;
    }
    input{
        width: 100%;
        border: none;
        padding: calc(v.$separacion/4);
        border-radius: 1rem;
        &:disabled{
            background-color: lighten(v.$negro, 20%);
            color: v.$blanco;
            cursor: not-allowed;
        }
    }
  
}